/* Rules for sizing the icon. */
.material-icons.md-16 { font-size: 16px; }
.material-icons.md-18 { font-size: 18px; }
.material-icons.md-24 { font-size: 24px; }
.material-icons.md-36 { font-size: 36px; }
.material-icons.md-48 { font-size: 48px; }

/* Rules for using icons as black on a light background. */
.material-icons.md-dark { color: rgba(0, 0, 0, 0.54); }
.material-icons.md-dark.md-inactive { color: rgba(0, 0, 0, 0.26); }

/* Rules for using icons as white on a dark background. */
.material-icons.md-light { color: rgba(255, 255, 255, 1); }
.material-icons.md-light.md-inactive { color: rgba(255, 255, 255, 0.3); }

.modal-60w {
    width: 720px !important;
    max-width: none!important;
}
.modal-content{
    padding: 1rem 1rem 0rem;
}

.container-avatar{
    position: relative;
    width: 80px;
    height: 80px;
    display: inline-block;
}
.container-avatar img{
    position: absolute;
    top: -20px;
    left: -48px;
}

.professional-data li small{
    font-size: 13px;
    display: block;
    margin-bottom: .5rem;
    color: rgba(0,0,0,.5)!important
}

.nav-pills .nav-link {
    padding: .5rem 1rem;
    border-radius: 3rem!important;
    border: 1px solid rgba(139,173,207,.62);
    color: #4e6679;
    font-size: .9rem;
    font-weight: 500!important;
    margin-right: .6rem;
    margin-bottom: .5rem;
}
.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    color: #1e88e5!important;
    background-color: #ecf5ff!important;
    border: 1px solid #92c6f3!important;
}
.nav-link {
    display: block;
    padding: .5rem 1rem;
}
.nav-link small {
    display: none;
}
.nav-link.active small {
    display: inline-block;
}

.hover-edit{
    position: relative;
}
.hover-edit:hover{
    background-color: #F7F7F7;
}
.hover-edit .edit-button{
    position: absolute;
    top: 15px;
    right: 15px;
    display: none;
}
.hover-edit:hover .edit-button{
    width: 42px;
    height: 42px;
    display: flex;
    border-radius: 40px;
    text-decoration: none;
    color: #4E6679;
    background-color: #F7F7F7;
}
.hover-edit:hover .edit-button .material-icons{
    margin: auto;
}
.hover-edit:hover .edit-button:hover{
    background-color: #FFFFFF;
}

.list-unstyled .hover-edit .edit-button{
    top: 10px;
    right: 15px;
}

.sticky-bottom{
    display: inline-block;
    width: 100%;
    background-color: white;
    padding: 0.3rem 0 0;
    position: sticky;
    bottom: 0;
}

.text-area-autosize{
    min-height: 100px !important;
}